<template>
  <div class="card">
    <div class="card-header">
      Exam07ComputedProperties
    </div>
    <div class="card-body">
      <p>name: {{name}}</p>
      <p>ssn1: {{ssn1}}</p>
      <p>ssn2: {{ssn2}}</p>
      <p>ssn: {{ssn1}}-{{ssn2.charAt(0) + "******"}}</p>
      <p>ssn: {{getSsn()}}</p>
      <p>ssn: {{computedSsn}}</p>
      <hr/>
      <button class="btn btn-info btn-sm mr-2" @click="changeName">변경</button>
      <button class="btn btn-info btn-sm mr-2" @click="changeSsn2">변경</button>
    </div>
  </div>   
</template>

<script setup>
import {ref,computed} from "vue";

//반응형 속성 선언
const name = ref("홍길동");
const ssn1 = ref("880515");
const ssn2 = ref("1110345");

//계속된 속성: 리렌더링 되더라도 ssn1과 ssn2가 변경이 없으면 실행x
const computedSsn = computed(()=>{
    console.log("computedSsn() 실행");
    const ssn= ssn1.value + "-" + ssn2.value.charAt(0) + "******";
    return ssn;
});

//일반 함수: 리렌더링될 때마다 실행
function getSsn(){
    console.log("getSsn() 실행");
    const ssn = ssn1.value + "-" + ssn2.value.charAt(0) + "******";
    return ssn;
}

//리렌더링을 위해 이름 수정
function changeName(){
    name.value += "동";
}

//계산된 속성이 실행되도록 ssn2수정
function changeSsn2(){
    ssn2.value += "2";
}

</script>